@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
	margin: 0;
	font-family: 'Roboto', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	padding: 50px;
	color: #2c2c2c;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}


.counter {
	width: 100px;
	height: 37px;
	background: #e2e9f0;
	border-radius: 40px;
	display: flex;
	align-items: center;
	justify-content: space-around;
	font-family: 'Roboto';
	font-style: normal;
	font-weight: normal;
	font-size: 24px;
	line-height: 23px;
	margin: 0 40px 0 20px;
}

.btn {
	font-size: 24px;
}
.btn-big {
	font-size: 34px;
}
.btn:hover {
	-webkit-transform: scale(1.3);
	        transform: scale(1.3);
}

.disable {
	color: gray;
}

.btn-gold-medium {
	background: linear-gradient(261.65deg, #eabd4b -3.95%, #e5aa15 109.44%);
	border-radius: 30px;
	font-style: normal;
	font-weight: 600;
	font-size: 18px;
	line-height: 21px;
	padding: 5px 110px;
	color: white;
	border: none;
	text-decoration: none;
	transition: 0.3s all ease-in-out;
}

.btn-gold-medium:hover,
.btn-gold-small:hover {
	/* transform: translateY(-3px); */
	background: linear-gradient(261.65deg, #c8a959 -3.95%, #b08924 109.44%);
	box-shadow: 3px 3px 5px#eecc7789;
}
.btn-gold-medium:active,
.btn-gold-small:active {
	background: linear-gradient(261.65deg, #f5d482 -3.95%, #f3c03e 109.44%);
}

.btn-gold-small {
	background: linear-gradient(261.65deg, #eabd4b -3.95%, #e5aa15 109.44%);
	border-radius: 30px;
	font-style: normal;
	font-weight: 600;
	font-size: 14px;
	line-height: 21px;
	padding: 3px 20px;
	color: white;
	border: none;
	text-decoration: none;
	transition: 0.3s ease-in-out all;
}

.cardFrame {
	position: relative;
	width: 575px;
	background: #f8fafc;
	border-radius: 25px;
	padding: 50px 30px 40px 30px;
	display: flex;
	flex-direction: column;
	align-content: center;
	justify-content: space-around;
	transition: 0.3s all ease-in-out;
}
.cardFrame:hover {
	box-shadow: 0px 4px 35px #c3d4e4;
}

.dd-wrapper {
	width: 258px;
	min-height: 30px;
	position: absolute;
	z-index: 10;
	padding: 5px 10px 5px 0;
	border-radius: 10px;
	background-color: #fff;
}
.dd-header {
	margin-bottom: 10px;
}
.dd-header-title {
	padding: 3px 0 3px 10px;
	display: flex;
	text-align: center;
	justify-content: space-between;
	font-weight: bold;
}

.dd-list-item {
	border-radius: 0 10px 10px 0;
	margin-bottom: 5px;
	padding: 5px 0 5px 10px;
	font-weight: bold;
}

.dd-list-item:hover {
	background-color: rgb(243, 248, 252);
}

.mainImage {
	margin: 0 auto;
	width: 265px;
	height: 265px;
	border-radius: 25px;
	overflow: hidden;
	margin-left: 80px;
}

.circle {
	flex: 0 0 auto;
	width: 50px;
	height: 50px;
	position: absolute;
	right: 0px;
	top: 0px;
	border-radius: 50%;
	display: flex;
	align-items: center;
	justify-content: center;
	box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
	transition: 0.3s all ease-in-out;
}
.circle:hover {
	box-shadow: 2px 2px 15px rgba(0, 0, 0, 0.2);
}

.container {
	display: block;
	position: relative;
	padding-left: 35px;
	margin-bottom: 12px;
	cursor: pointer;
	font-size: 22px;
	-webkit-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

.container input {
	position: absolute;
	opacity: 0;
	cursor: pointer;
}

.checkmark {
	box-sizing: border-box;
	position: absolute;
	top: 0;
	left: 0;
	height: 25px;
	width: 25px;
	background-color: #eee;
	border-radius: 5px;
	transition: all 0.3s ease-in-out;
}

/* On mouse-over, add a grey background color */
.container:hover input ~ .checkmark {
	background-color: rgb(193, 186, 186);
}

/* When the radio button is checked, add a blue background */
.container input:checked ~ .checkmark {
	background: linear-gradient(261.65deg, #efbb37 -3.95%, #cf9e1f 109.44%);
	border: 5px solid rgb(193, 186, 186);
	border-radius: 5px;
}

/* Create the indicator (the dot/circle - hidden when not checked) */
.checkmark:after {
	content: '';
	position: absolute;
	display: none;
}

/* Show the indicator (dot/circle) when checked */
.container input:checked ~ .checkmark:after {
	display: none;
}

/* Style the indicator (dot/circle) */
.container .checkmark:after {
	top: 9px;
	left: 9px;
	width: 8px;
	height: 8px;
	border-radius: 50%;
	background: white;
}

